.cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center
}

.home {
    height: 100vh;
}

.arrow {
    font-size: 18px;
    background-color: #fff;
    border-style: none;
    width: 30px;
    height: 30px;
    border-radius: 10%;
    margin: 5px;
    color: rgb(45, 103, 230);
}

.arrow:hover {
    font-size: 18px;
    background-color: rgb(45, 103, 230);
    border-style: none;
    width: 30px;
    height: 30px;
    border-radius: 10%;
    margin: 5px;
    color: #fff;
}

.main {
    color: #fff;
    font-size: 18px;
    background-color: rgb(49, 137, 238);
    border-color: rgb(88, 180, 226);
    border-style: none;
    width: 30px;
    height: 30px;
    border-radius: 10%;
    margin: 5px
}

@media only screen and (max-width: 500px) {
    .cards {
        grid-template-columns: 100%;
    }
}